import React from "react";
import PropTypes from "prop-types";

import "./navItem.scss";

const NavItem = (props) => (
  <li className="nav__li">
    <a href={props.link} className="nav__item">
      {props.children}
    </a>
  </li>
);

NavItem.propTypes = {
  class: PropTypes.string,
  link: PropTypes.string.isRequired,
};

export default NavItem;
