import React from "react";
import PropTypes from "prop-types";

import NavItem from "./NavItem/NavItem";

import "./navigation.scss";

const Navigation = (props) => {
  return (
    <nav className={`nav ${props.open ? "is-open" : "is-hidden"}`}>
      <ul className="nav__list">
        <NavItem link="/">Home</NavItem>
        <NavItem link="/">Our Story</NavItem>
        <NavItem link="/">Venue</NavItem>
        <NavItem link="/">Travel</NavItem>
        <NavItem link="/">Things to Do</NavItem>
        <NavItem link="/">FAQ</NavItem>
        <NavItem link="/">RSVP</NavItem>
      </ul>
    </nav>
  );
};

Navigation.propTypes = {
  open: PropTypes.bool.isRequired,
};

export default Navigation;
